#carousel.swiper-container {
  width: 100%;
  height: 100%;
}

#carousel .swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

#carousel .swiper-slide img {
  width: 100%;
  max-height: 450px;
}

#carousel .swiper-slide .title {
  background-color: rgba(0, 0, 0, 0);
  bottom: 0;
  right: 20px;
  float: right;
  position: absolute;
  width: auto;
  z-index: 9999;
  color: #FFFFFF;
  font-size: 1em;
  padding: 1rem;
  margin-bottom: 1em;
  transition: bottom .8s ease,background-color .8s ease,visibility .2s;
  visibility: hidden;
}

@media only screen and (max-width: 768px) {
  #carousel .swiper-slide img {
    min-height: 250px;
  }
  #carousel .swiper-slide .title {
    font-size: .8em;
    padding: .5rem;
  }
}

